const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
//获取应用实例
const app = getApp()
export default {
	name: '',
	data() {
		return {
			banner: [],
			channel: [],
			searchIcon: true,
			hotlist: [],
			keyword: ""
		}	
	},
	onLoad() {
		this.getAdList();
	},
	// 页面显示
	onShow() {
		
	},
	// 页面分享
	onShareAppMessage() {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/home/home?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	//下拉刷新
	onPullDownRefresh(e){
		setTimeout(function () {
			wx.stopPullDownRefresh();
		}, 1000);
	},
	
	methods: {
		//初始化banner
		getAdList() {
			util.login().then(res => {
				return util.request(api.AdBannerList, { code: 'Home_Top' }, 'post').then(res => {
					if(res.code === 0) {
						this.banner = res.data;
						this.getCateList();
						this.getHotList();
						this.getShareTicket()
					}
				})
			})
		},
		// 初始化分类
		getCateList() {
			util.request(api.categoryList,{}, 'post').then(res=>{
				if(res.code === 0) {
					this.channel = res.data.list;
				}
			})
		},
		//获取热门商品
		getHotList() {
			util.request(api.HotGoodsList,{total: 10},'post').then(res=>{
				if(res.code === 0) {
					this.hotlist = res.data;
				}
			})
		},
		dealPrcture(str) {
			let newStr = str.split(',');
			return newStr[0]
		},
		//input 聚焦
		searchInt(e) {
			this.keyword = e.detail.value;
		},
		searchConfirm(e) {
			wx.navigateTo({
				url: '/pages/goodlist/list?keyword='+e.detail.value
			})
		},
		
		
		//获取分享数据得到的群信息
		getShareTicket() {
			let ticket = wx.getStorageSync('ticket');
			let pages = getCurrentPages();
			let { options } = pages.pop();
			var that = this;
			if(ticket && options.code != "") {
				wx.getShareInfo({
					shareTicket: ticket,
					success:function(res) {
						let params = {
							code: options.code ? options.code : "",
							iv: res.iv,
							data: res.encryptedData
						}	
						console.log(params)
						that.getShareInfos(params)
					}
				})
			}
		},
		
		//新增分享记录
		getShareInfos(params) {
			util.request(api.setShareRechge, params,'post').then(res=>{
				console.log(res)
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	}
}
